import React, { PureComponent } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { isTagShop } from "../../../../actions/api";
import Image from "../../../Themes/Elements/Media/Image";
import ReelVideo from "./ReelVideo";

const mediaDimension = JSON.parse(
  window.localStorage.getItem("mediaDimension")
);

export default class ReelMediaSlider extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      mediaSize: {
        height: mediaDimension != null ? mediaDimension[0] : "85vh",
        width: mediaDimension != null ? mediaDimension[1] : "85vh",
      }
    }
  }
  mediaSize = (mediaSize) => this.props.mediaSizeCallback(mediaSize);

  render() {
    const { Post, wallID, size, onUpdateProdImgIdx, inView } = this.props;
    const muted = false;

    return (
      <div key={Post.id} className="tb_rp_media_holder">
        <Splide
          options={{
            direction: "ltr",
            pagination: true,
            arrows: false,
            speed: 400,
            rewind: true,
            perPage: 1,
            perMove: 1,
            gap: 0,
            updateOnMove: true,
            wheel: false, // Disable mouse wheel
            paginationDirection: "ltr",
            classes: {
              pagination: `splide__pagination tb_rp_bullet`,
            },
          }}
          onMoved={(splide, index) => onUpdateProdImgIdx(index)}
        >
          {Post.mediaList.map((imageItem, index) => (
            <SplideSlide key={index}>
              {[2, 4].includes(imageItem.type) ? (
                <Image
                  ImageClass="tb_rp_image"
                  Post={Post}
                  mediaOnly={imageItem}
                  size={size}
                  multiPic={1}
                  multiKey={index}
                  hotspot={imageItem.product.length ? isTagShop : false}
                  inView={true}
                />
              ) : [3, 5].includes(imageItem.type) ? (
                  <ReelVideo
                    VideoClass="tb_rp_video"
                    media={imageItem.video.full}
                    mediaPoster={imageItem.image.original}
                    Post={{...Post, media : { video : imageItem.video , image : imageItem.image }}}
                    wallID={wallID}
                    size={size}
                    isCover={true}
                    controls={false}
                    autoPlay={true}
                    muted={muted}
                    index={index}
                    inView={inView}
                  />
              ) : null}
            </SplideSlide>
          ))}
        </Splide>
      </div>
    );
  }
}