import React, { PureComponent } from "react";
import ReactPlayer from "react-player"
import { isTagShop } from "../../../../actions/api"; 
import { reelPlayTimeUpdateData } from "../../../../utils/Common";
export default class ReelVideo extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            paddingBottom: 100,
            videoLoaded: false,
            played: 0
        }
    }
    setVideoLoaded = event => {  
        this.setState({
            videoLoaded: event
        })
    }

    componentWillUnmount() {
        const { played } = this.state
        if (isTagShop) reelPlayTimeUpdateData(played)
    }

    onUpdateTime = (progress) => {   
        try {
            if (isTagShop) this.setState({ played: this.state.played + progress.playedSeconds })
        }
        catch (e) {
            console.log(e)
        }
    }

    render() {
        const { VideoClass, Post, controls, handleVideoEnded, muted, currentView, inView } = this.props
        const { videoLoaded } = this.state
        const { media, height, width, id, link, networkId } = Post
        const videoStyle = {
            paddingBottom: `100vh`,
        };

        return <div className={`${VideoClass}_wrap_`} style={videoStyle}> 
            <ReactPlayer
                key={`${id}-${currentView}-${link}`}
                className={`${VideoClass} tb_media-${id}`}
                url={inView ? media.video.full : media?.image?.small}
                data-height={height}
                data-width={width}
                data-type="video"
                data-network={networkId}
                data-link={link}
                data-item-id={id}
                data-load={0}
                config={{
                    facebook: {
                    appId: '117029975354072',
                    attributes: {
                        'data-width': width,
                        'data-height': height,
                        poster: media?.image?.small,
                    },
                    },
                    file: {
                    attributes: {
                        preload: 'auto',
                        poster: media?.image?.small,
                        playsInline: true, // Add playsinline attribute
                    },
                    },
                }}
                autoPlay={currentView ? 1 : 0}
                loop={true}
                muted={currentView && muted} // Mute the video for autoplay
                volume={currentView && muted ? 0 : 1}
                playsinline={true} // Add playsinline prop
                playing={currentView} // Autoplay when `autoPlay` is true
                height={'100%'}
                width={'100%'}
                controls={controls}
                onReady={() => this.setVideoLoaded(true)}
                onProgress={this.onUpdateTime}
                onEnded={handleVideoEnded || null}
                />

            {!videoLoaded ? <div className='tb_rpp_v_spinner'><div></div></div> : null}
        </div>
    }
}



