import React, { Suspense, memo } from "react";
import { isTagShop } from "../../../../actions/api";
import Image from "../../../Themes/Elements/Media/Image";
import ReelVideo from "./ReelVideo";
import ReelMediaSlider from "./ReelMediaSlider";

const ReelMedia = (props) => {
  const { Post, muted, index, wallID, inView, currentView } = props;
  const size = false;

  return (
      Post.mediaList && Post.mediaList.length > 0 ? (
          <ReelMediaSlider Post={Post} {...props} />
          ) : [2,4].includes(Post.type) && inView ? (
              <Image
                ImageClass="tb_rp_image"
                Post={Post}
                size={size}
                hotspot={Post?.product?.length ? isTagShop : false}
                blurBG={false}
                observer={false}
                index={index}
                inView={inView}
              />
          ) : [3,5].includes(Post.type) && inView ? (
              <ReelVideo
                VideoClass="tb_rp_video"
                Post={Post}
                size={size}
                wallID={wallID}
                isCover={true}
                controls={false}
                currentView={currentView}
                muted={muted}
                handleVideoEnded={() => {}}
                index={index}
                inView={inView}
              />
          ) : null
  );
};
export default memo(ReelMedia);
