import React, { Component, memo } from "react";
import { isTagShop, suiteTrack } from "../../../actions/api";
import MagentoCart from '../CartPopUp/Magento'
import CartPopUp from '../CartPopUp'
import ReelBody from "./ReelBody";
import { connect } from "react-redux";
import { isValidAuthorImage } from "../../../utils/Common";
import { nextData, themePostTracking } from "../../../actions/moredataAction";
import { detectIphone } from "../../../utils";
import { Splide, SplideSlide } from '@splidejs/react-splide';
import './scss/ReelSplide.scss'

class PostPopUp extends Component {
    constructor(props) {
        super(props);
        this.sliderRef = React.createRef();
        this.state = {
            currentIndex: this.props?.currentIndex,
            item: this.props.item,
            windowWidth: window.innerWidth,
            isAuthorImagevalid: true,
            isHotspot: false, addtoCartPopup: false,
            allProductDetails: null, selectedProductId: null,
            selectedProductDetails: null,
            continueShopping: false,
            selectedVariant: null,
            showAddToCart: false,
            productLoading: true,
            productImg: '',
            tagshopProductDeatil: null,
            muted: detectIphone() ? true : false, // By Default Mute for iPhone
            activeSlide: 0,
            addCartSetTime: false,
            selectedProdDetails: null, tagProdDetail: null
        }
        this.splideRef = React.createRef()
    }

    componentDidMount() {
        const { currentIndex, item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid, });
    }

    setMuted = () => this.setState({ muted: !this.state.muted, })
    // setOnProductDetails = (data) => this.setState({ allProductDetails: data })
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { currentIndex, item } = nextProps; const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid });
    }
    onMediaUpdate = () => this.setState({ isAuthorImagevalid: false });
    onAddToCart = (itemDetails, id, product, item) => {
        const { wall } = this.props;
        const { webId, wallId, ownerId, } = wall.ThemeInfo
        setTimeout(() => {
            this.setState({
                addCartSetTime: true
            })
        }, 100);

        const trackingData = {
            type: 1, action: 2,
            wall: isTagShop ? webId : wallId,
            feed: item.feedId,
            post: item.referenceId ? item.referenceId : item.id,
            owner: ownerId,
            product_id: product.id,
            currency: product.price, price: product.discount > 0 ? product.discount : product.price
        }


        this.setState({ allProductDetails: itemDetails, addtoCartPopup: true, selectedProductDetails: itemDetails[id], tagshopProductDeatil: { ...product, trackingData } })
    }


    requestData = () => {
        const { wall, appendData, hasMoreData, loaderData } = this.props;
        const { webId, wallId } = wall.ThemeInfo
        const postCount = wall.ThemeStyle.totalPosts;
        const timeStamp = Math.floor(Date.now() / 1000);
        if (hasMoreData && !loaderData.isShowMoreLoading) this.props.nextData(isTagShop ? webId : wallId, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent)
    }

    onItemChange = (item) => this.setState({ item })


    startSplideSlide = (splide) => {
        this.setState({ activeSlide: splide.index })
    }

    currentSplideSlide = (splide) => {
        this.onPopUpTrack(splide.index)
        this.setState(
            { activeSlide: splide.index }
        )
        if (this.props.hasMoreData && splide.index == this.props.completeData.length - 2) { this.requestData() }
    }

    onPopUpTrack = (currentIndex) => {
        try {
            const { wall, completeData } = this.props
            const { ThemeInfo } = wall

            let item = completeData[currentIndex] ? completeData[currentIndex] : null;
            if (item) {
                const requestData = { feed: item.feedId, post: item.referenceId ? item.referenceId : item.id }
                const ugcPayload = { action: 2, wall: ThemeInfo.webId, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, type: 3, owner: ThemeInfo.ownerId }
                themePostTracking(suiteTrack ? ugcPayload : { action: 2, wall: ThemeInfo.webId, ...requestData, owner: ThemeInfo.ownerId, type: 3 })
            }
        }
        catch (error) {
            console.log("Error in PopUp Tracking", error)
        }
    }
    onClickClosePopUp = (e) => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null, addCartSetTime: false })
    onClickCheckOut = (e) => this.setState({ addtoCartPopup: true, continueShopping: false })

    render() {
        const { onClosePopUp, slideWithArrowKeys, wall, completeData } = this.props;
        const { currentIndex, item, addtoCartPopup, selectedProductDetails, continueShopping, productImg, tagshopProductDeatil, addCartSetTime, muted, activeSlide } = this.state;

        return <div className={`modalId${item.id} tb_rp_modal ${addCartSetTime ? 'tb_rp_add_cart_m' : ''}`} onKeyDown={slideWithArrowKeys} onKeyPress={slideWithArrowKeys} onLoad={slideWithArrowKeys} onClick={(e) => e.currentTarget == e.target && onClosePopUp(e)} >
            <div className="tb_rp_dialog">
                {!(addtoCartPopup || continueShopping) ?
                    <div className="tb_reel_close_btn" onClick={onClosePopUp}>
                        <div className='tb__icon tb-close-thin'><div></div></div>
                    </div> : null}

                <div className="tb_time_line_wrap">
                    <div className="tb_time_line"><div></div></div>
                </div>

                <Splide
                    ref={this.splideRef}
                    options={{
                        direction: 'ttb', // Vertical sliding (top-to-bottom)
                        height: window.innerHeight, // Set height
                        start: currentIndex, // Initial slide
                        wheel: false, // Enable mousewheel scrolling
                        releaseWheel: true, // Allow free scrolling
                        sensitivity: 1, // Mousewheel sensitivity
                        drag: true, // Enable touch dragging
                        pagination: false, // Disable pagination
                        arrows: false, // Disable arrows
                        isNavigation: false, // Disable navigation
                        updateOnMove: true, // Update on slide move
                    }}
                    onMoved={this.currentSplideSlide} // Slide change event
                    onMounted={this.startSplideSlide} // Splide initialization event
                >
                    {completeData && completeData.length > 0 ? completeData.map((item, index) => {
                        return <SplideSlide key={`${item.id}-${index}`}>
                            <ReelBody
                                currentView={activeSlide === index}
                                key={`${item.id}-${index}`}
                                onClosePopUp={onClosePopUp}
                                wall={wall}
                                item={item}
                                openAddtocart={(itemDetails, id, product) => this.onAddToCart(itemDetails, id, product, item)}
                                onsetProductImg={(img) => this.setState({ productImg: img })}
                                // setOnProductDetails={this.setOnProductDetails}
                                muteToggle={this.setMuted}
                                muted={muted}
                                inView={Math.abs(activeSlide - index) <= 2}
                            />
                        </SplideSlide>
                    })
                        : null}
                </Splide>
            </div>

            {addtoCartPopup && selectedProductDetails ?
                <>
                    <div className="tb_rp_cart_overlay"><div></div></div>
                    <div className="tb_rp_cart_modal">
                        <div className="tb_rp_cart_body">
                            <div className="ts_reel_close_btn tb_reel_close_btn" onClick={addtoCartPopup && !continueShopping ? this.onClickClosePopUp : continueShopping ? event => this.setState({ addtoCartPopup: true, continueShopping: false }) : null}>
                                <div className='tb__icon tb-close-thin'><div></div></div>
                            </div>

                            <div className="tb_rp_cart_wrap">
                                {selectedProductDetails && Object.keys(selectedProductDetails).length > 0 ? tagshopProductDeatil?.createdFrom == "magento" ? <MagentoCart productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProductDetails} tagshopProductDeatil={tagshopProductDeatil} closeCart={event => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} closePopUp={this.props.onClosePopUp} /> : <CartPopUp productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProductDetails} tagshopProductDeatil={tagshopProductDeatil} closeCart={event => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} closePopUp={this.props.onClosePopUp} /> : null}
                            </div>
                        </div>
                    </div>
                </>
                : null}
        </div>
    }
}

const mapStateToProps = state => {
    const { appData } = state;
    return {
        loaderData: state.loaderData,
        appendData: appData.postData.appendData,
        hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
    }
}

const mapDispatchToProps = dispatch => {
    return {
        nextData: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(memo(PostPopUp));