import React, { memo,  useState } from 'react';
import ReelProducts from './Elements/ReelProducts'
import ReelMedia from './Elements/ReelMedia'
import ReelBranding from './Elements/ReelBranding';

const ReelBody = ({ wall, item, onsetProductImg, openAddtocart,  index, currentView, muteToggle, muted, inView }) => {
    const isVideo = [3,5].includes(item.type) ? true : false
    const { ThemeInfo, ProductSetting } = wall
    const [activeIndex, setactiveIndex] = useState(0)

    const onUpdateProdImgIdx=(Idx)=>{
        setactiveIndex(Idx)
    }

    const GetProducts=(products)=>{

        return <div className='tb_rpp_slide_wrap'>
            {products.products && products.products.map((product, index) => {
                return <ReelProducts key={index} product={product} openAddtocart={openAddtocart} onsetProductImg={onsetProductImg} wall={wall} item={item} />
            })}
            </div>
    }

    return <><ReelMedia Post={item} wallID={ThemeInfo.wallId} onUpdateProdImgIdx={onUpdateProdImgIdx} ownerId={ThemeInfo.ownerId} ThemeID={ThemeInfo.id} wall={wall} muted={muted} currentView={currentView} index={index} inView={inView}/>

            <div className='tb_rp_content'>  
                { ProductSetting &&  item.isProduct ? <GetProducts products={ item.mediaList?.length ? item.mediaList?.[activeIndex]?.product?.length ? item.mediaList[activeIndex].product : [] : item.products} /> : null }
                {isVideo ?
                <div className="tb_rp_mute_btn" onClick={muteToggle}>
                    <div className={`tb__icon tb-${!muted ? `unmute` : `mute`}`}><div></div></div>
                </div>
                : null}
                {ThemeInfo.branding ? <ReelBranding wallId={ThemeInfo.wallId} themeID={ThemeInfo.id} /> : null}
            </div>
        </>
}
export default memo(ReelBody);